// Category Selector
.category-selector {
  float: left;
  padding-left: 2.1875rem;
  margin-bottom: 0;
  width: 100%;
  list-style: none;

  @include media-min(370px) {
    width: 50%;
  }

  @include media-breakpoint-up(lg) {
    margin-right: 1.5625rem;
    width: auto;
  }

  @include media-breakpoint-up(xl) {
    margin-right: 3.75rem;
  }

  &:last-of-type {
    margin-right: 0;
  }

  li {
    margin-bottom: 0.9375rem;
    height: 100%;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0.625rem;
    }
  }

  .custom-control {
    padding-left: 0;
  }

  .custom-control-label {
    padding-left: 0.5625rem;
  }

  .custom-checkbox {
    .custom-control-input:disabled ~ .custom-control-label {
      cursor: not-allowed;
    }
  }
}

.size-selector,
.color-selector {
  padding-left: 0;
  margin-left: -0.625rem;
  margin-right: -0.625rem;
  margin-bottom: 0;
  list-style: none;

  @include media-breakpoint-up(lg) {
    margin-left: -0.1563rem;
    margin-right: -0.1563rem;
  }

  @include media-breakpoint-up(xl) {
    margin-left: -0.3125rem;
    margin-right: -0.3125rem;
  }

  .custom-control {
    padding-left: 0;
  }

  .custom-checkbox {
    .custom-control-label {
      width: 3.125rem;
      height: 3.125rem;
      border-radius: 50%;
      border: 0.0625rem solid #e4e4e4;
      cursor: pointer;
    }

    .custom-control-input:disabled ~ .custom-control-label {
      cursor: not-allowed;
    }
  }

  li {
    margin: 0 0.625rem 1.25rem;
    float: left;

    @include media-breakpoint-up(lg) {
      margin: 0.1563rem;
    }

    @include media-breakpoint-up(xl) {
      margin: 0.3125rem;
    }

    &:last-child {
      .custom-control {
        margin-right: 0;
      }
    }
  }
}

// Color Selector
.color-selector {
  .custom-checkbox {
    .custom-control-label {
      border: none;

      &::before {
        display: none;
      }

      &::after {
        @include center(both);
      }
    }
  }
}

// Size Selector
.size-selector {
  .custom-checkbox {
    .custom-control-label {
      &::before,
      &::after {
        display: none;
      }

      span {
        @include center(both);

        font-size: 0.75rem;
        line-height: 1.25rem;
      }
    }

    .custom-control-input:checked ~ .custom-control-label {
      background-color: $body-color;

      span {
        color: $white;
      }
    }

    .custom-control-input:disabled ~ .custom-control-label {
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        height: 0.0625rem;
        width: 100%;
        display: block;
        border: none;
        background-image: linear-gradient(to right, #292929 40%, rgba($body-color, 0) 20%);
        background-position: top;
        background-size: 0.1875rem 0.0625rem;
        background-repeat: repeat-x;
        opacity: 0.5;
        transform: translate(-50%, -50%) rotate(225deg);
      }
    }
  }
}

// Product Filter
//
// ================================================
.product-filter {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1051;
  width: 100%;
  height: 100%;
  padding: 0 1.875rem 1.25rem;
  background-color: $body-bg;

  @include media-breakpoint-up(lg) {
    @include center(horizontal);

    top: auto;
    z-index: 2;
    height: auto;
    padding: 0;
  }

  & > .container {
    @include media-breakpoint-up(lg) {
      max-width: calc(100% - 3.75rem);
    }

    @include media-breakpoint-up(xl) {
      max-width: 75.625rem;
    }

    @include media-breakpoint-up(xxl) {
      max-width: 90.625rem;
    }
  }

  .filter-container,
  .sort-container {
    @include media-breakpoint-up(lg) {
      border-top: 0.0625rem solid $green;
    }

    .column {
      width: 100%;
      border-bottom: 0.0625rem solid $green;

      @include media-breakpoint-up(lg) {
        padding-left: 0.9375rem;
        padding-right: 0.9375rem;
        width: auto;
        border-bottom: none;
      }

      .title {
        position: relative;
        padding: 1.25rem 0;
        margin-bottom: 0;
        cursor: pointer;

        @include media-breakpoint-up(lg) {
          padding: 0;
          margin-bottom: 0.5rem;
          cursor: default;
        }

        &::after {
          content: "+";
          position: absolute;
          right: 0.25rem;

          @include media-breakpoint-up(lg) {
            display: none;
          }
        }

        &.is-open {
          &::after {
            content: "-";
          }
        }
      }
    }
  }

  // Filter Container
  .filter-container {
    @include media-breakpoint-up(lg) {
      padding-top: 1.4375rem;
      padding-bottom: 1.4375rem;
    }

    @include media-breakpoint-up(xl) {
      padding-left: 0.625rem;
      padding-right: 0.625rem;
    }

    .column {
      @include media-breakpoint-up(lg) {
        float: left;
        margin-right: 0.9375rem;
      }

      @include media-breakpoint-up(xl) {
        margin-right: 3.125rem;
      }

      &:last-of-type {
        margin-right: 0;
      }
    }

    .color-selector {
      @include media-breakpoint-up(lg) {
        max-width: 13.75rem;
      }

      @include media-min(1200px) {
        max-width: 21.25rem;
      }

      @include media-breakpoint-up(xl) {
        max-width: 22.8125rem;
      }
    }

    .size-selector {
      @include media-breakpoint-up(lg) {
        max-width: 19.0625rem;
      }
    }
  }

  // Sort Container
  .sort-container {
    @include media-breakpoint-up(lg) {
      padding-top: 1.25rem;
      padding-bottom: 1.25rem;
    }

    .btn-group {
      .btn {
        border-radius: 1.25rem;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        margin-left: 0.3125rem;
        margin-right: 0.3125rem;
        margin-bottom: 1.25rem;

        @include media-breakpoint-up(lg) {
          margin-bottom: 0;
        }
      }
    }
  }

  // Button Container
  .button-container {
    .row .column {
      position: relative;
      padding: 0.9375rem 0;
      width: 100%;
    }

    hr {
      margin-top: 0;
      margin-bottom: 0;
      border-top: 0.0625rem solid $green;
    }

    .btn-clear {
      color: $body-color;
      font-size: 0.875rem;
      letter-spacing: 0.44px;
      line-height: 1.875rem;
      text-decoration: underline;
    }

    .btn-icon {
      margin-left: 0.625rem;

      @include media-min(23.125rem) {
        position: absolute;
        right: 0;
        margin-left: 0;
      }
    }
  }
}

// JS Filter
.js-filter-toggle {
  .filter-close {
    display: none;
  }
}

.js-filter-collapse {
  display: none;
}

.filter-is-open {
  @include media-breakpoint-down(md) {
    overflow: hidden !important;
    overflow-y: hidden !important;
  }
}

.js-accordion-body {
  @include media-breakpoint-down(md) {
    display: none !important;
  }
}

.js-accordion-toggle {
  &.is-open {
    & + .js-accordion-body {
      @include media-breakpoint-down(md) {
        display: block !important;
      }
    }
  }
}

.admin-bar {
  .product-filter {
    top: 2.875rem;
    height: calc(100% - 2.875rem);

    @include media-min(48.9375rem) {
      top: 2rem;
      height: calc(100% - 2rem);
    }

    @include media-breakpoint-up(lg) {
      top: auto;
      height: auto;
    }
  }
}



